<template>
  <div>
    <core-progress-modal :show="loading" />
    <v-tabs
      v-if="!loading"
      color="primary"
      dark
      icons-and-text
    >
      <v-tabs-slider color="white" />

      <v-tab href="#fila">
        Fila Cobrança
        <v-icon>mdi-human-queue</v-icon>
      </v-tab>

      <v-tab
        v-if="['ADMIN', 'SUPERVISOR'].includes($root.roleAtiva())"
        href="#mailing"
      >
        Mailing Configuração
        <v-icon>mdi-cog</v-icon>
      </v-tab>

      <v-tab-item
        value="fila"
        lazy
      >
        <fila-cobranca
          :clientes-acordo="clientesAcordo"
          :faturas="faturas"
          :atraso-mes="atrasoMes"
        />
      </v-tab-item>
      <v-tab-item
        value="mailing"
        lazy
      >
        <mailing-cobranca
          :clientes-acordo="clientesAcordo"
          :faturas="faturas"
          :atraso-mes="atrasoMes"
        />
      </v-tab-item>
    </v-tabs>
  </div>
</template>

<script>
import { TiposBusiness } from '../../business'
import { TODAS_ID } from '../../constants'

const FilaCobranca = () => import('./FilaCobranca.vue')
const MailingCobranca = () => import('./MailingCobranca.vue')

export default {
  metaInfo: {
    titleTemplate: '%s - Cobrança'
  },
  components: {
    FilaCobranca,
    MailingCobranca
  },
  data () {
    return {
      clientesAcordo: [
        { id: '', descricao: 'Todos' },
        { id: 'em_acordo', descricao: 'Apenas os em Acordo' },
        { id: 'exceto_acordo', descricao: 'Exceto os em Acordo' }
      ],
      faturas: [
        { id: '', descricao: 'Todos' },
        { id: 'em_aberto', descricao: 'Em Aberto' },
        { id: 'adimplente', descricao: 'Adimplente' },
        { id: 'inadimplente', descricao: 'Inadimplente' }
      ],
      atrasoMes: [
        { id: TODAS_ID, descricao: 'Todos' },
        { id: 0, descricao: 'Sem Atraso' },
        { id: 30, descricao: '1 a 30 dias' },
        { id: 60, descricao: '31 a 60 dias' },
        { id: 90, descricao: '61 a 90 dias' },
        { id: 120, descricao: '91 a 120 dias' },
        { id: 150, descricao: '121 a 150 dias' },
        { id: 180, descricao: '151 a 180 dias' },
        { id: 360, descricao: '181 a 360 dias' },
        { id: 390, descricao: '361 a 390 dias' },
        { id: 391, descricao: 'Acima a 390' }
      ],
      servicoSPCList: [],
      redes: [],
      loading: false,
      remessaId: '0'
    }
  },
  beforeMount () {
    this.loading = true
    this.redes.push({ id: TODAS_ID, descricao: 'Todas Redes' })
    TiposBusiness.getAllRedes()
      .then(response => {
        this.redes = this.redes.concat(response.data)
      })
      .finally(() => {
        TiposBusiness.getAllServicoSPC()
          .then(response => { this.servicoSPCList = response.data })
          .catch(() => { this.$root.showErro('Não foi possível buscar as informações dos motores de créditos') })
          .finally(() => {
            this.loading = false
          })
      })
    if (this.$route.params.remessaId) {
      this.remessaId = this.$route.params.remessaId
    }
  }
}
</script>
